@import url('https://fonts.googleapis.com/css2?family=Lora:ital@1&family=Play:wght@700&family=Saira+Condensed:wght@500;600&display=swap');

* {
    margin: 0;
    padding: 0;
}

html,
body {
    font-family: 'Play', sans-serif;
    overflow: hidden;
    background-color: rgb(0, 0, 0);
}

.webgl {
    position: fixed;
    top: 0;
    left: 0;
    outline: none;
}

.social-list {
    margin: 2rem;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    gap: 1rem;
    position: fixed;
    bottom: 0;
    right: 0;
}


.twitter {
    height: 50px;
    width: 50px;
    background-color: white;
    border-radius: 50%;
    background-image: url('../static/icons/twitter.svg');
    background-size: 40px 40px;
    background-repeat: no-repeat;
    background-position: center;
}

.mail {
    height: 50px;
    width: 50px;
    background-color: white;
    border-radius: 50%;
    background-image: url('../static/icons/poo.svg');
    background-size: 40px 40px;
    background-repeat: no-repeat;
    background-position: center;

}

.telegram {
    height: 50px;
    width: 50px;
    background-color: rgb(255, 255, 255);
    border-radius: 50%;
    background-image: url('../static/icons/telegram.svg');
    background-size: 40px 40px;
    background-repeat: no-repeat;
    background-position: center;
}

.cursor {
    width: 20px;
    height: 20px;
    display: none;
    position: fixed;
    background-color: white;
    border-radius: 50%;
    transition: 0.1s ease;
    transform: translate(-50%, -50%);
    pointer-events: none;
    mix-blend-mode: difference;
    z-index: 100000;
}

.hovered {
    width: 60px;
    height: 60px;
}

.ca {
    display: flex;
    justify-content: center;
    align-items: center;
    /* position: fixed; */
    bottom: 5%;
    right: 10%;
    gap: 10px;
    border-radius: 10%;
}

.text {
    color: rgb(0, 0, 0);
    background-color: aliceblue;
    border-radius: 10%;
    padding: 10px;
    max-width: 50vw;
    font-size: 1vw;
}

.ca button {
    padding: 10px;
    font-size: 20px;
    font-weight: 1000;
}

#copybtn {
    border-radius: 10%;
}


@media (max-width : 600px) {
    .text {
        font-size: 2vw;
    }
}